p {
    font-style: italic;
    font-size: 1.25em;
}

.description {
    margin: 0 2.5% 0 2.5%;
    grid-row-start: 4;
}
nav {
    background-color: rgb(215, 215, 215);
    padding: 0 5% 0 0;
}

.nav-wrapper {
    grid-area: nav;
}

#navL {
    font-size: 1.25em;
    color: forestgreen;
}

#logo {
    -webkit-transform: translateY(-30%);
            transform: translateY(-30%);
    max-height: 250px;
}

#hamburger {
    font-size: 2.5em;
    color: forestgreen;
}

#nav-mobile {
    -webkit-transform: translateY(35%);
            transform: translateY(35%);
    color: #608832;
}

@media only screen and (min-width: 768px) {
    nav {
        height: 100px;
    }
  }

  @media only screen and (min-width: 360px) and (max-width: 767px) {
    nav {
        height: 75px;
    }

    #hamburger {
        font-size: 2em;
    }
  }
#smIcon {
    max-width: 2em;
    color: forestgreen;
    margin: 1em;
}

.page-footer {
    background-color:rgb(215, 215, 215);
    border-top: 2px solid rgb(190, 188, 188);
    padding: 0;
    color: forestgreen;
}

.copyright {
    padding: 0 0 1em 0;
    font-size: .75em;
}

@media only screen and (min-width: 320px) and (max-width: 767px) {
    .icons {
        margin: 0 auto;
        text-align: center;
    }
}

@media only screen and (min-width: 768px) {
    .icons {
        margin-right: 10%;
    }
  }
